
          @import "src/assets/styles/_vars.scss"; 
        
.jokes-screen {
  > .wrapper {
    position: relative;
    height: 100%;

    .background {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      z-index: -2;
      height: 100%;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: 50% 50%;
      }
    }

    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      background-color: rgba($grey, 0.3);
    }
  }

  .scroll-container {
    height: 100%;
    overflow: auto;
    position: relative;

    .inner {
      padding-bottom: 75px;
    }
  }

  .arrow-alignment-block {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 20;
  }

  .arrow-up-container {
    width: calc(100% - 30px);
    position: relative;

    @media (min-width: 1480px) {
      width: 100%;
      max-width: 100%;
    }
  }

  .arrow-up {
    width: 60px;
    height: 60px;
    background-color: rgba($blue, 0.65);
    border-radius: 50%;
    position: absolute;
    bottom: 32.5px;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    z-index: 10;
    opacity: 0;
    transform: translateX(200px);

    @media (min-width: 1480px) {
      right: 32.5px;
    }

    @media (max-width: 1024px) {
      bottom: 15px;
    }

    &.enter {
      opacity: 0;
      transform: translateX(200px);
    }

    &.enter-active {
      opacity: 1;
      transition: 0.3s;
      transform: translateX(0);
    }

    &.enter-done {
      opacity: 1;
      transform: translateX(0);
    }

    &.exit {
      opacity: 1;
      transform: translateX(0);
    }

    &.exit-active {
      opacity: 0;
      transition: 0.3s;
      transform: translateX(200px);
    }

    &.exit-done {
      opacity: 0;
      transform: translateX(200px);
    }

    svg {
      font-size: 25px;
      color: $grey;
    }
  }
}
