
          @import "src/assets/styles/_vars.scss"; 
        
.tasks-widget {
  position: relative;
  display: flex;
  min-height: 160px;
  width: calc(55% - 15px);
  padding: 15px;
  background: rgba($black, 0.9);
  border-radius: 30px;
  overflow: hidden;

  @media (max-width: 640px) {
    order: 3;
    padding: 10px;
    border-radius: 15px;
    width: 100%;
  }

  .tasks-wrapper {
    display: flex;
    flex-direction: column;
    flex: 1;
    max-width: 100%;
    padding: 30px 15px;
    border-radius: 30px;
    gap: 15px;
    background-color: rgba($white, 0.1);
    align-items: center;
    justify-content: center;

    @media (max-width: 640px) {
      border-radius: 15px;
      padding: 10px;
      gap: 10px;
    }

    &.enter {
      opacity: 0;
      transform: translateX(100%);
    }

    &.enter-active {
      opacity: 1;
      transform: translateX(0);
      transform-origin: right;
      transition: 0.15s;
    }

    &.exit {
      opacity: 1;
      transform: translateX(0);
    }

    &.exit-active {
      opacity: 0;
      transform: translateX(100%);
      transform-origin: right;
      transition: 0.15s;
    }

    h2 {
      color: $white;
      font-size: 30px;
      text-align: center;

      @media (max-width: 1024px) {
        font-size: 28px;
      }

      @media (max-width: 640px) {
        font-size: 22px;
      }

      @media (max-width: 390px) {
        font-size: 18px;
      }
    }
  }

  .tasks-link {
    color: $sky-blue;
    text-decoration: none;
    font-size: 20px;
    text-align: center;

    @media (max-width: 1024px) {
      font-size: 18px;
    }

    @media (max-width: 640px) {
      font-size: 16px;
    }

    @media (max-width: 390px) {
      font-size: 14px;
    }
  }

  .tasks-list {
    width: 100%;
    overflow: hidden;
  }

  .tasks-item {
    @media (max-width: 640px) {
      min-height: 36px;
    }

    .check-button {
      @media (max-width: 640px) {
        width: 24px;
        height: 24px;
      }
    }

    .delete-button {
      @media (max-width: 640px) {
        font-size: 22px;
      }
    }

    .value {
      @media (max-width: 640px) {
        width: calc(100% - 51.13px);
        padding: 6px;
        font-size: 18px;
      }

      @media (max-width: 390px) {
        font-size: 16px;
      }
    }

    .inner,
    .editor {
      @media (max-width: 640px) {
        padding: 4px 6px;
      }
    }
  }

  .no-active-tasks-banner {
    display: flex;
    flex-direction: column;
    flex: 1;
    max-width: 100%;
    padding: 30px 15px;
    border-radius: 30px;
    gap: 15px;
    background-color: rgba($white, 0.1);
    align-items: center;
    justify-content: center;

    @media (max-width: 640px) {
      border-radius: 15px;
      padding: 10px;
      gap: 10px;
    }

    &.enter {
      opacity: 0;
      transform: translateX(100%);
    }

    &.enter-active {
      opacity: 1;
      transform: translateX(0);
      transform-origin: right;
      transition: 0.15s;
    }

    &.exit {
      opacity: 1;
      transform: translateX(0);
    }

    &.exit-active {
      opacity: 0;
      transform: translateX(100%);
      transform-origin: right;
      transition: 0.15s;
    }

    h2 {
      color: $white;
      font-size: 30px;
      text-align: center;

      @media (max-width: 1024px) {
        font-size: 28px;
      }

      @media (max-width: 640px) {
        font-size: 22px;
      }

      @media (max-width: 390px) {
        font-size: 18px;
      }
    }
  }

  .no-active-tasks-link {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
    text-decoration: none;

    svg {
      color: $sky-blue;
      font-size: 40px;
    }

    span {
      color: $sky-blue;
      text-decoration: none;
      font-size: 20px;
      text-align: center;

      @media (max-width: 1024px) {
        font-size: 18px;
      }

      @media (max-width: 640px) {
        font-size: 16px;
      }

      @media (max-width: 390px) {
        font-size: 14px;
      }
    }
  }
}
